	constructor(props) {
		super(props);

		this.state = {
			highlightedItemId: null,
			activeMarkerId: null,
			defaultCenter: null,
		};
		this.coordinations = [];
		this.highlightMenuItem = this.highlightMenuItem.bind(this);
		this.handleMapIt = this.handleMapIt.bind(this);
	}

	highlightMenuItem(id) {
		this.setState({ highlightedItemId: id });
	}

	handleHideMarker() {
		this.setState({
			activeMarkerId: null,
		});
	}

	handleMapIt(id, longitude, latitude) {
		if (!this.state.activeMarkerId) {
			this.setState({
				activeMarkerId: id,
				defaultCenter: {
					lng: longitude,
					lat: latitude,
				},
			});
		} else {
			this.setState({
				activeMarkerId: null,
				defaultCenter: null,
			});
		}
	}

	handleLike() {
		/* action to api endpoints*/
	}

